<template>
  <div class="page" id="ieoAddPage">
    <index-top 
      :h1="'KISwap'"
      :h4="$t('index.最具潜力的集DEX、IKO、DAO、聚合交易为一体的DEFI生态平台')"></index-top>
    <div class="form-block">
      <div class="form-title">{{$t('ieo.设置参数')}}</div>
      <div class="cell-tit">{{$t('ieo.设置众筹代币')}}</div>
      <div class="cell-block cell-block--four">
        <!-- <div class="cell-row flex-middle">
          <div class="label">{{$t('ieo.选择代币')}}</div>
          <div class="cont flex-item">
          <el-select v-model="xuanzedaibi" placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          </div>
        </div> -->
        <div class="cell-row flex-middle">
          <div class="label">{{$t('ieo.代币地址')}}</div>
          <div class="cont flex-item">
            <input type="text" v-model="project.tokenAddress" :placeholder="$t('ieo.代币地址')">
          </div>
        </div>
        <div class="cell-row flex-middle">
          <div class="label">{{$t('ieo.冷钱包地址')}}</div>
          <div class="cont flex-item">
            <input type="text" v-model="project.userAddress" :placeholder="$t('ieo.冷钱包地址')">
          </div>
        </div>
        <div class="cell-row flex-middle">
          <div class="label">{{$t('ieo.众筹代币数')}}</div>
          <div class="cont flex-item">
            <input type="number" v-model="project.tokenAmount" :placeholder="$t('ieo.众筹代币数')">
          </div>
        </div>
      </div>
      <div class="cell-tit">{{$t('ieo.设置众筹销售价格')}}</div>
      <div class="cell-block">
        <div class="cell-row flex-middle">
          <div class="label">KIS</div>
          <div class="cont flex-item">
            <input type="number" v-model="project.price" :placeholder="$t('ieo.1-100000的整数')">
          </div>
        </div>
        <div class="cell-row flex-middle">
          <div class="label">KIS</div>
          <div class="cont flex-item">
            <input type="number" disabled v-model="project.total" :placeholder="$t('ieo.目标筹集资金')">
          </div>
        </div>
      </div>
      <div class="cell-tit">{{$t('ieo.设置众筹参数')}}</div>
      <div class="cell-block cell-block--three">
        <div class="cell-row flex-middle">
          <div class="label">{{$t('ieo.众筹开始时间')}}</div>
          <div class="cont flex-item">
            <el-date-picker
              v-model="project.startTime"
              type="datetime"
              format="YYYY-MM-DD HH:mm"
              :placeholder="$t('ieo.众筹开始时间')">
            </el-date-picker>
          </div>
        </div>
        <div class="cell-row flex-middle">
          <div class="label">{{$t('ieo.众筹结束时间')}}</div>
          <div class="cont flex-item">
            <el-date-picker
              v-model="project.stopTime"
              type="datetime"
              format="YYYY-MM-DD HH:mm"
              :placeholder="$t('ieo.众筹结束时间')">
            </el-date-picker>
          </div>
        </div>
      </div>
      <div class="form-btn" v-if="address" @click="addProject">{{$t('ieo.发起')}}</div>
      <div class="form-btn" v-else @click="initWallet">{{$t('ieo.连接钱包')}}</div>
    </div>
  </div>
</template>
<script>
import IndexTop from "@/views/index/IndexTop";
import { reactive, toRefs, computed, watch} from "vue";
import ieoAbi from "@/utils/ieoAbi";
import BigNumber from "bignumber.js";
import tokenAbi from "@/utils/abi";
import web3Data from "@/utils/initWeb3";
import {useStore} from "vuex";
import config from "@/utils/config";
import poolList from "@/utils/poolList";
import { ElMessage } from 'element-plus';
import { useI18n } from "vue-i18n";
export default {
  components: {
    IndexTop
  },
  setup () {
     const store = useStore();
     const { t } = useI18n()
     const data = reactive({
       $t: t,
       themeMode: computed(() => store.state.themeMode),
       poolPairList: poolList,
       rate: computed(() => store.state.rate),
       web3: computed(() => store.state.provider),
       address: computed(() => store.state.accounts[0]),
       project: {
         tokenAddress: '',
         userAddress: '',
         tokenAmount: '',
         price: '',
         total: computed(() => {
           if (data.project.tokenAmount && data.project.price) {
             return data.project.tokenAmount/data.project.price
           }
           return 0
         }),
         startTime: '',
         stopTime: ''
       },
       initWallet: () => {
         web3Data.initWeb3()
       },
       addProject: async () => {
         if (!data.project.tokenAddress) {
           ElMessage.error(data.$t('ieo.请输入代币地址'));
           return
         }
         if (!data.project.userAddress) {
           ElMessage.error(data.$t('ieo.请输入冷钱包地址'));
           return
         }
          if (!data.project.tokenAmount) {
           ElMessage.error(data.$t('ieo.请输入众筹代币数'));
           return
         }
         if (!data.project.price) {
           ElMessage.error(data.$t('ieo.请输入兑换价格'));
           return
         }
         if (!data.project.startTime) {
           ElMessage.error(data.$t('ieo.请输入开始时间'));
           return
         }
         if (!data.project.stopTime) {
           ElMessage.error(data.$t('ieo.请输入结束时间'));
           return
         }
         try {
          let tokenContract = new data.web3.eth.Contract(tokenAbi, data.project.tokenAddress)
          let decimals = await tokenContract.methods.decimals().call();
          let amount = new BigNumber(data.project.tokenAmount).multipliedBy(Math.pow(10, decimals)).toFixed()
          let startTime = new Date(data.project.startTime).getTime() / 1000 + 600
          let stopTime = new Date(data.project.stopTime).getTime() / 1000
          let ieoContract = new data.web3.eth.Contract(ieoAbi, config.ieo)
          await ieoContract.methods.addProject(
            data.project.tokenAddress,
            data.project.userAddress,
            data.project.price,
            startTime,
            stopTime,
            amount
            ).send({from: data.address})
         } catch (error) {
           ElMessage.error(data.$t('ieo.代币地址错误'));
         }
         
       }
     })
    
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="scss">
#ieoAddPage {
  .el-select,
  .el-input {
    width: 100%;
  }
  .el-input {
    font-size: 24px;
    .el-input__inner {
      background: transparent;
      border: 0;
      text-align: right;
      padding-right: 50px;
      color: #fff;
    }
    .el-select__caret {
      width: 26px;
      height: 18px;
      vertical-align: middle;
    }
    .el-icon-arrow-up:before {
      content: '';
      display: block;
      height: 100%;
      background: url('~@/assets/image/ieo/ic_arrow_nor.png') no-repeat center;
      background-size: contain;
    }
    .el-input__prefix {
      left: auto;
      right: 0;
      .el-input__icon {
        width: 30px;
      }
      .el-icon-time:before {
        content: '';
        display: block;
        height: 100%;
        background: url('~@/assets/image/ieo/icon.png') no-repeat center;
        background-size: contain;
      }
    }
  }
  .el-radio-group {
    width: 100%;
    text-align: right;
    .el-radio-button {
      .el-radio-button__inner {
        border: 1px solid rgba(255, 198, 0, .3);
        background: transparent;
        border-radius: 0;
        font-size:22px;
        color: #FFC600;
        margin: 10px;
      }
      .el-radio-button__orig-radio:checked+.el-radio-button__inner {
        box-shadow: none;
        background: rgba(255, 198, 0, .3);
      }
    }
  }
}
.theme-light {
  #ieoAddPage {
    .el-input {
      .el-input__inner {
        color: #111111;
      }
      .el-icon-arrow-up:before {
        background-image: url('~@/assets/image/light/ieo/ic_arrow_nor.png');
      }
      .el-input__prefix {
        .el-icon-time:before {  
          background-image: url('~@/assets/image/light/ieo/icon.png');
        }
      }
    }
    .el-radio-group {
      .el-radio-button {
        .el-radio-button__inner {
          border: 1px solid rgba(173, 120, 5, .3);
          color: #AD7805;
        }
        .el-radio-button__orig-radio:checked+.el-radio-button__inner {
          background: rgba(173, 120, 5, .25);
        }
      }
    }
  }
}
@media screen and (max-width: 480px) {
  #ieoAddPage {
    .el-input {
      font-size: 14px;
      .el-input__inner {
        padding-right: 35px;
      }
      .el-select__caret {
        width: 18px;
        height: 18px;
        margin: 10px 0 0;
      }
      .el-input__prefix .el-input__icon {
        width: 18px;
      }
    }
    .el-radio-group .el-radio-button .el-radio-button__inner {
      font-size: 14px;
      margin: 4px;
      padding: 8px 12px;
    }
  }
}
</style>
